//es6  promise uni-appapi

export default (params)=> {
	//
	uni.showLoading({
		title:""
	})
	
	return new Promise((resolve,reject)=> {
		uni.request({
			...params,
			success: (res) => {
				resolve(res.data);
			},
			fail(err) {
				reject(err)
			},
			complete() {
				// complete
				uni.hideLoading();
			}
			
		})
	})
}